	<view class="popup">
		<view class="popup-top">
			<view class="title">一键农作</view>
		</view>
		<view class="popup-swiper">
			<swiper class="popup-swiper " @change="handleSwiperChange" :current="swiperCurrent">
				<swiper-item v-for="(item,index) in order" :key="index" @click="handleIndex(index)">
					<view class="popup-middle" :style="{background: index==bgcolor ?'rgba(0, 208, 143, 0.05)':'#fff'}">
						<view class="title">
							<view class="text">{{item.PackageName}}</view>
							<view class="money">￥{{item.Price}}</view>
						</view>
						<view class="content">
							<view class="text-left">{{item.PackageDescribe}}</view>
							<view class="text-right">
								套餐价
							</view>
						</view>
						<view class="price">
							<view class="tops" v-for="(item,index) in item.ProductList" :key="index">
								<image :src="item.ProductImage" mode=""></image>
								<view class="image-right">
									<view class="text">{{item.ProductName}}</view>
									<view class="money">￥{{item.UnitPrice}}</view>
								</view>
							</view>

						</view>
					</view>
				</swiper-item>
			</swiper>
			<view class="dots">
				<block v-for="(item,index) in order.length" :key="index">
					<view class="dot" :class="index==swiperCurrent ? ' active' : ''"></view>
				</block>
			</view>
		</view>
		<!-- <view class="popup-botttom">
			<textarea placeholder="备注" maxlength="-1" class="textarea" />
		</view> -->
		<view class="popup-bottom">
			<view class="btn" @click="handlePay">
				<text>立即支付</text>
				<text>(￥{{money}})</text>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		props: {
			order: {
				default: {}
			}
		},
		data() {
			return {
				swiperCurrent: 0,
				current: 0,
				money: 0,
				packageId: '',
				bgcolor: null,
			}
		},
		async onShow() {

		},
		methods: {
			/**
			 * 支付
			 */
			/**
			 * 轮播
			 * @param {Object} e
			 */
			/**
			 * 轮播
			 * @param {Object} e
			 */
			handleSwiperChange(e) {
				this.swiperCurrent = e.detail.current
			},
			async handleIndex(index) {
				this.bgcolor = index
				this.money = this.order[index].Price
				this.packageId = this.order[index].PackageID
			},
			handlePay() {
				if (!this.packageId) {
					uni.showToast({
						title: '请选择套餐!',
						icon: 'none'
					})
					return false
				}
				this.$emit('onPay', this.packageId)
				// uni.navigateTo({
				// 	url: '/pages/index/purchase/index'
				// })
			},

		}
	}
</script>

