	.popup {
		padding-bottom: 0rpx;
		z-index: 20;
		background-color: #fff;
		border-radius: 30rpx 30rpx 0 0;
		padding: 0 30rpx;
	}

	.popup-top {
		padding: 36rpx 0 0;
		text-align: center;

		.titlt {
			font-size: 34rpx;
			font-weight: 600;
			color: #333333;
			line-height: 48rpx;
		}
	}

	.popup-swiper {
		height: 560rpx;
		position: relative;

		.dots {
			position: absolute;
			bottom: 20rpx;
			left: 50%;
			transform: translate(-50%, 0);
			-webkit-transform: translate(-50%, 0);
			z-index: 99;
			display: flex;
			flex-direction: row;
			justify-content: center;

			.dot {
				width: 10rpx;
				height: 10rpx;
				background: #CCCCCC;
				// transition: all .6s;
				margin-right: 10rpx;
				border-radius: 50%;
			}

			.active {
				width: 30rpx;
				height: 10rpx;
				background: #00D08F;
				border-radius: 5rpx;
			}
		}
	}

	.popup-middle {
		height: 410rpx;
		margin-top: 34rpx;
		padding: 30rpx 20rpx;
		// background: rgba(0, 208, 143, 0.05);
