					padding-left: 30rpx;
					box-sizing: border-box;
					border-left: 2rpx solid #efefef;
				}
			}

			// }

			.price-right {
				display: flex;
				justify-content: start;
				flex-wrap: wrap;

				.tops {
					width: 100%;
					display: flex;
					justify-content: start;
					margin-left: 30rpx;

					&:nth-child(odd) {
						// &:nth-of-type(odd) {
						margin-bottom: 34rpx;
					}

					&:nth-of-type(even) {
						margin-left: 50rpx;
						border-left: 2rpx solid #efefef;
					}
				}
			}

			.image-right {
				padding-left: 30rpx;

				.text {
					font-size: 28rpx;
					font-weight: 400;
					color: #333333;
					line-height: 40rpx;
				}

				.money {
					font-size: 28rpx;
					font-weight: 500;
					color: #FEB042;
					line-height: 40rpx;
					padding-top: 46rpx;
				}
			}

			image {
				width: 130rpx;
				height: 130rpx;
				border-radius: 20rpx;
			}
		}
	}

	.popup-botttom {
